@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap');

body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo-title-layout {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.logo-title-font {
  font-weight: 700;
  font-size: 48px;
  color: #5B3080;
}

.top-bar-layout {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 3rem;
}

.nav-bar-font {
  font-weight: 500;
  font-size: 20px;
}

.nav-bar-font li a {
  color: #171717;
}

.home-join-font {
  font-weight: 400;
  font-size: 25px;
  color: #171717;
}

.home-join-layout {
  margin: auto;
  width: 51%;
  text-align: center;
}

.home-join-button-font {
  font-weight: 700;
  font-size: 18px;
}


